import React, { useState, useEffect, useRef, useCallback } from 'react';
import './App.css';
import {
    View,
    ScrollView,
    Text,
    Image,
    TextInput,
    Pressable,
    TouchableOpacity
  } from 'react-native-web';
import { createReducer, usePrevious } from 'react-use';
import logger from 'redux-logger'
import querystring from 'query-string';

import './App.css';

const LOCAL_STORAGE_ITEM_KEY = '@AURORA_APP_STATE_2';

const localStorageMiddleware = ({ getState }) => next => action => {
  next(action);

  const stateAsString = JSON.stringify(getState())
  window.localStorage.setItem(LOCAL_STORAGE_ITEM_KEY, stateAsString);
}
const useMagicReducer = createReducer(
  logger,
  localStorageMiddleware
);

const therapist_start_sequence = "Hi I'm Aurora, what brought you in today?"
const prompt_starters = {
  Melissa: 'The following is an online therapy session between Aurora, an excellent and highly skilled therapist, and her client, Melissa, a 31 year old white woman. Aurora specializes in treating depression and anxiety. Her therapy style involves active listening, teaching her clients new skills and challenging their beliefs.',
  E: 'The following is an online therapy session between Aurora, an excellent therapist, and her client. Aurora specializes in human behavior and mental disorders, and uses a variety of techniques to give thoughtful answers to her client.'
}
let therapy_style = 'E'

const initialMessages = [
  buildMessage(
    therapist_start_sequence,
    false
  )
]

const initialSession = [buildSession(initialMessages)]
console.log(initialSession);

const INITIAL_STATE = {
  apiKey: null,
  messages: initialMessages,
  sessions: initialSession,
  userName: null,
  userGender: null,
  userAge: null,
  userRace: null,
  userSexuality: null,
  userMentalHealthConcerns: [],
  userPreferences: [],
  menuClosed: true,
  popUp: true,
}

const ACTIONS = {
  ADD_MESSAGE: 'ADD_MESSAGE',
  SET_API_KEY: 'SET_API_KEY',
  SET_USER_NAME: 'SET_USER_NAME',
  SET_USER_GENDER: 'SET_USER_GENDER',
  SET_USER_AGE: 'SET_USER_AGE',
  SET_USER_RACE: 'SET_USER_RACE',
  SET_USER_SEXUALITY: 'SET_USER_SEXUALITY',
  SET_USER_MH_CONCERNS: 'SET_USER_MH_CONCERNS',
  SET_USER_PREFS: 'SET_USER_PREFS',
  TOGGLE_MENU: 'TOGGLE_MENU',
  TOGGLE_POPUP: 'TOGGLE_POPUP',
  START_NEW_SESSION: 'START_NEW_SESSION',
  RESET_APP_STATE: 'RESET_APP_STATE'
}

const reducer = (state, action) => {
  switch (action.type) {
    case ACTIONS.ADD_MESSAGE:
      const lastSession = getLastSession(state)
      lastSession.messages = [...lastSession.messages, action.payload.message]
      console.log(state.sessions);
      return {
            ...state,
            sessions: [
              ...state.sessions.slice(0,state.sessions.length-1),
              lastSession
            ]
          };
    case ACTIONS.SET_API_KEY:
      return {
        ...state,
        apiKey: action.payload.apiKey
      };
    case ACTIONS.TOGGLE_MENU:
      return {
        ...state,
        menuClosed: action.payload
      };
    case ACTIONS.TOGGLE_POPUP:
      return {
        ...state,
        popUp: action.payload
      };
    case ACTIONS.RESET_APP_STATE:
      return INITIAL_STATE;
    default:
      return state;
  }
}

// MAIN APP

function App({ initialState }) {
  const [state, dispatch] = useMagicReducer(reducer, initialState);

  const prevMessages = usePrevious(getLastSession(state).messages) || [];
  const scrollViewRef = useRef();

  
  const fetchGPTAnswer = useCallback(async (input) => {
    // fetch GPT answer
    console.log('going to ask gpt');
    console.log('prompt: ', createPrompt(state));

    const requestOptions = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + String(state.apiKey)
      },
      body: JSON.stringify({
        'prompt': createPrompt(state),
        'temperature': 0.9,
        'max_tokens': 150,
        'top_p': 1,
        'frequency_penalty': 0,
        'presence_penalty': 0.6,
        'stop': ['AURORA:','CLIENT:'],
      })
    };
    fetch('https://api.openai.com/v1/engines/text-davinci-003/completions', requestOptions)
        .then(response => response.json())
        .then(data => {
          console.log(data);
          // build message
          const nextMessage = buildMessage(
              data.choices[0].text.trim(),
              false
            );
          // set messages
          dispatch({
            type: ACTIONS.ADD_MESSAGE,
            payload: { message: nextMessage }
          })
    }).catch(err => {
      console.log(err);
          // build message
          console.log(err.message);
          const nextMessage = buildMessage(
              "Sorry my servers are overloaded right now - can you try again in a little bit?",
              false
            );
          // set messages
          dispatch({
            type: ACTIONS.ADD_MESSAGE,
            payload: { message: nextMessage }
          })
    });
  }, [state, dispatch]);

  useEffect(() => {
    if (getLastSession(state).messages.length !== prevMessages.length) {
      // Scroll messages to bottom
      scrollViewRef.current.scrollToEnd({ animated: false });
    }
  }, [state, dispatch, prevMessages])

  useEffect(() => {
    const isLastMessageFromUser = getLastMessage(getLastSession(state).messages)?.isUser === true;
    console.log(isLastMessageFromUser);
    if (isLastMessageFromUser) {
      if (isValidApiKey(state.apiKey))  {
          console.log('sending to gpt', getLastMessage(getLastSession(state).messages).message);
          fetchGPTAnswer(getLastMessage(getLastSession(state).messages).message);
        }
        else {
          console.log('api key check failed');
          // set api from urls parms
          const query = querystring.parse(window?.location?.search);
        
          if (query.a) {
            const decodedApiKey = atob(query.a);
            if (isValidApiKey(decodedApiKey)) {
              dispatch({
                  type: ACTIONS.SET_API_KEY,
                  payload: { apiKey: decodedApiKey }
                })
              console.log('key is valid: ', isValidApiKey(state.apiKey));
            }
          }
      }
    }
  }, [state, dispatch, fetchGPTAnswer]);

  return (
    <Container>
        <Menu state={state} dispatch={dispatch} />
        <Header />
        <Chat sessions={state.sessions} scrollViewRef={scrollViewRef} />
        <Footer state={state} dispatch={dispatch} />
   </Container>
  );
}


