// HELPER FUNCTIONS 
  
  function buildMessage(string, isUser = true) {
    return {
      isUser,
      message: string,
      date: +new Date()
    }
  }

  function buildSession(messages = []) {
    return {
      startTime: new Date().toLocaleString('en-US', {weekday: 'short', month: 'short', day: 'numeric', hour: 'numeric', minute: '2-digit'}),
      messages,
      notes: null,
      active: true
    }
  }
  
  function getLastMessage(messages) {
    return messages[messages.length - 1];
  }

  function getLastSession(state) {
    const sessionCt = state.sessions.length
    const lastSession = state.sessions[sessionCt - 1]
    return lastSession
  }
  
  function createPrompt(state) {
    const lastSession = getLastSession(state)
    console.log(lastSession);
    const messages = lastSession.messages
    console.log(messages);

    let prompt = prompt_starters[therapy_style] + '\n\nAURORA: ' + therapist_start_sequence + '\n';
    for (let i = 1; i < messages.length; i++) {
      let obj = messages[i];
      if (obj.isUser) {
        prompt += 'CLIENT: ' + obj.message + '\n';
      } else {
        prompt += 'AURORA: ' + obj.message + '\n';
      }
    }
    prompt += 'AURORA: '
    return prompt
  }
  
  function isValidApiKey(string) {
    if (string === null) {
      return false
    }
    return string.substring(0, 2) === "sk";
  }
  
  function clearHistory() {
    window.localStorage.clear()
  }

// APP LOADER

function AppLoader() {
  const [isLoaded, setIsLoaded] = useState(false);
  const [initialState, setInitialState] = useState();

  useEffect(() => {
    const stateAsString = window.localStorage.getItem(LOCAL_STORAGE_ITEM_KEY);

    if (!stateAsString) {
      setInitialState(INITIAL_STATE)
    } else {
      setInitialState(JSON.parse(stateAsString))
    }


    setIsLoaded(true);
  }, [setInitialState, setIsLoaded]);

  return isLoaded
    ? <App initialState={initialState} />
    : <LoadingScreen />;
}

function LoadingScreen() {
  return (
    <Text>Loading...</Text>
  )
}

export default AppLoader;